<template>
  <za-page-content scroll="true">
    <za-table ref="table" :data="accounts" :total="pageConfig.total" @selection-change="handleSelectionChange" @action-change="changePage" :isLoading="loadingTable">
      <div style="padding-bottom: 0px;" slot="searchBar">
        <el-form :model="filters" ref="filtersForm">
          <el-form-item>
            <el-col :span="8">
              <el-col :span="8">资讯号ID:</el-col>
              <el-col :span="14">
                <el-input v-model="filters.id" :clearable="true" placeholder="资讯号ID"></el-input>
              </el-col>
            </el-col>
            <el-col :span="8">
              <el-col :span="8" :offset="1">资讯号昵称:</el-col>
              <el-col :span="14">
                <el-input v-model="filters.nickName" :clearable="true" placeholder="搜索昵称..."></el-input>
              </el-col>
            </el-col>
            <el-col :span="8">
              <el-col :span="6" :offset="2">状态:</el-col>
              <el-col :span="14">
                <el-select style="width:100%" v-model="filters.status" placeholder="请选择状态">
                  <el-option v-for="item in accountStatus" :key="item.value" :label="item.label" :value="item.value">
                  </el-option>
                </el-select>
              </el-col>
            </el-col>
          </el-form-item>
          <el-form-item>
            <el-col :span="8">
              <el-col :span="8">资讯号类型:</el-col>
              <el-col :span="14">
                <el-select style="width:100%" v-model="filters.type" placeholder="请选择类型">
                  <el-option v-for="item in accountType" :key="item.value" :label="item.label" :value="item.value">
                  </el-option>
                </el-select>
              </el-col>
            </el-col>
            <el-col :span="8">
              <el-col :span="8" :offset="1">资讯号等级:</el-col>
              <el-col :span="14">
                <el-select style="width:100%" v-model="filters.level" placeholder="请选择等级">
                  <el-option v-for="item in accountLevel" :key="item.value" :label="item.label" :value="item.value">
                  </el-option>
                </el-select>
              </el-col>
            </el-col>
          </el-form-item>
          <el-form-item>
            <el-col :span='24'>
              <el-button icon="el-icon-search" type="primary" @click="filter">查询</el-button>
              <el-button type="success" @click="handleCreateAccount">新建账号</el-button>
            </el-col>
          </el-form-item>
        </el-form>
      </div>

      <el-table-column prop="id" label="资讯号ID" width="80"></el-table-column>
      <el-table-column prop="nickName" label="资讯号昵称"></el-table-column>
      <el-table-column prop="type" label="资讯号类型">
        <template slot-scope="scope">
          {{scope.row.type === '01' ? '爬取目标账号' : '众安官方账号'}}
        </template>
      </el-table-column>
      <el-table-column prop="status" label="状态" :formatter="formatterStatus"></el-table-column>
      <el-table-column prop="level" label="资讯号等级"></el-table-column>
      <el-table-column prop="openDate" width="160" :formatter="formatterDate" label="开通时间"></el-table-column>
      <el-table-column label="操作" width="120">
        <template slot-scope="scope">
          <el-button size="mini" type="primary" @click="handleEditAccount(scope.row)">编辑</el-button>
        </template>
      </el-table-column>
    </za-table>

    <!-- 新增 & 编辑 -->
    <el-dialog :title="dialogType === 'created' ? '新建账号': '编辑账号' " :visible.sync="showdalog" @close="hideDialog" width="500px">
      <el-row>
        <el-row>
          <el-form :model="subEdit" ref="form" :rules="formValidate" label-position="left" label-width="120px">
            <el-row type="flex" align="middle">
              <el-form-item label="资讯号类型:" prop="type" style="width: 100%">
                <el-col :span="20">
                  <el-select style="width:100%" v-model="subEdit.type" :disabled="dialogType === 'edit'" placeholder="请选择类型">
                    <el-option v-for="item in accountTypeEx" :key="item.value" :label="item.label" :value="item.value">
                    </el-option>
                  </el-select>
                </el-col>
              </el-form-item>
            </el-row>
            <el-row type="flex" align="middle">
              <el-form-item label="昵称:" prop="nickName" style="width: 100%">
                <el-col :span="20">
                  <el-input v-model="subEdit.nickName" placeholder="请输入昵称" :disabled="dialogType === 'edit'"></el-input>
                </el-col>
              </el-form-item>
            </el-row>
            <el-row type="flex" align="middle">
              <el-form-item label="状态:" prop="status" style="width: 100%">
                <el-col :span="20">
                  <el-select style="width:100%" v-model="subEdit.status" placeholder="请选择状态">
                    <el-option v-for="item in accountStatusEx" :key="item.value" :label="item.label" :value="item.value">
                    </el-option>
                  </el-select>
                </el-col>
              </el-form-item>
            </el-row>
            <el-row type="flex" align="middle">
              <el-form-item label="等级:" prop="level" style="width: 100%">
                <el-col :span="20">
                  <el-select style="width:100%" v-model="subEdit.level" placeholder="请选择等级">
                    <el-option v-for="item in accountLevelEx" :key="item.value" :label="item.label" :value="item.value">
                    </el-option>
                  </el-select>
                </el-col>
              </el-form-item>
            </el-row>
          </el-form>
        </el-row>
        <div class="dialog-footer" style="text-align: center">
          <el-button @click="showdalog = false;" type="primary">取 消</el-button>
          <el-button @click="saveAccount" type="success">确 定</el-button>
        </div>
      </el-row>
    </el-dialog>
  </za-page-content>
</template>

<script>
import formatterDate from "../../filters/date/index.js";
import { ajaxPostCommon } from "../../api/common.js";
import * as ajaxUrl from "../../api/url";

const staticStatus = [
  { label: "启用", value: "01" },
  { label: "禁用", value: "02" },
  { label: "未审核", value: "03" }
];
const staticLevel = [
  { label: "S", value: "S" },
  { label: "A", value: "A" },
  { label: "B", value: "B" },
  { label: "C", value: "C" },
  { label: "D", value: "D" },
  { label: "E", value: "E" }
];

const staticType = [
  { label: "爬取目标账号", value: "01" },
  { label: "众安官方账号", value: "02" }
];

export default {
  data() {
    return {
      filters: {
        id: "",
        nickName: "",
        type: "",
        status: "",
        level: ""
      },
      subEdit: {},
      accounts: [],
      pageConfig: {
        pageNum: 1,
        pageSize: 10,
        total: 1
      },
      dialogType: "created",
      accountType: [
        { label: "全部", value: "" },
        { label: "爬取目标账号", value: "01" },
        { label: "众安官方账号", value: "02" }
      ],
      accountLevel: [
        { label: "全部", value: "" },
        { label: "S", value: "S" },
        { label: "A", value: "A" },
        { label: "B", value: "B" },
        { label: "C", value: "C" },
        { label: "D", value: "D" },
        { label: "E", value: "E" }
      ],
      // accountLevelEx: this.accountLevel.slice(0, 1),
      accountStatus: [
        { label: "全部", value: "" },
        { label: "启用", value: "01" },
        { label: "禁用", value: "02" },
        { label: "未审核", value: "03" }
      ],

      formValidate: {
        type: [
           { required: true, message: "请选择所属栏目", trigger: "blur" }
        ],
        nickName: [
           { required: true, message: "请输入昵称", trigger: "blur" }
        ],
        status: [
           { required: true, message: "请选择状态", trigger: "blur" }
        ],
        level: [
           { required: true, message: "请选择等级", trigger: "blur" }
        ],
      },
      accountStatusEx: staticStatus,
      accountTypeEx: staticType,
      accountLevelEx: staticLevel,
      showdalog: false
    };
  },

  methods: {
    filter() {
      this.pageConfig = {
        ...this.pageConfig,
        ...this.filters,
        pageNum: 1
      };
      if (!this.$refs.table.resetCurrentPage()) {
        this.initData();
      }
    },

    initData() {
      this.loadingTable = true;
      ajaxPostCommon(ajaxUrl.getAccountList, {
        ...this.pageConfig
      }).then(res => {
        setTimeout(() => {
          this.accounts = res.result.list;
          this.pageConfig.total = res.result.total;
          this.loadingTable = false;
        }, 200);
      });
    },

    // 创建账号
    handleCreateAccount() {
      this.showdalog = true;
      this.dialogType = "created";
    },

    // 编辑账号
    handleEditAccount(row) {
      this.showdalog = true;
      this.subEdit = { ...row };
      this.dialogType = "edit";
    },

    saveAccount() {
      let validatePass = false;
      this.$refs.form.validate(pass => {
        validatePass = pass;
      })
      if (!validatePass) return false;
      ajaxPostCommon(ajaxUrl.saveOrUpdateAccount, this.subEdit).then(data => {
        if (data.returnCode === "200") {
          this.hideDialog();
          this.$message({
            message: "保存成功!",
            type: "success"
          });
          this.initData();
        }
      });
    },

    hideDialog() {
      this.$refs.form.resetFields();      
      this.showdalog = false;
      this.subEdit = {};
    },

    handleSelectionChange(selection) {
      this.selectionRowData = selection;
    },

    formatterDate(r, c, v) {
      return formatterDate(v);
    },

    formatterStatus(r, c, v) {
      switch (v) {
        case "01":
          return "启用";
          break;
        case "02":
          return "禁用";
          break;
        case "03":
          return "未审核";
          break;
      }
    },

    changePage(params) {
      let pageParams = {
        pageNum: params.page,
        pageSize: params.rows || this.pageConfig.pageSize
      };
      this.pageConfig = { ...this.pageConfig, ...pageParams };
      this.initData();
    }
  },

  created() {
    this.initData();
  }
};
</script>

<style lang="scss" scoped>
</style>




